@import "../../assets/styles/fonts.css";
@import "../../assets/styles/colors.css";

.header {
    margin: 60px 0 140px 0;
    display: flex;
    justify-content: flex-end;
    color: var(--c-white);
}

.link {
    font-family: "Manrope-Medium";
    font-size: 24px;
    line-height: 32px;

    margin-left: 32px;
    padding-bottom: 1px;
    border-bottom: solid 2px transparent;
    cursor: pointer;
    transition: 0.5s;
}

.link:hover {
    border-bottom: solid 2px white;
    transition: 0.5s;
}

.burger, .burger_one, .burger_two {
    display: none;
}

.burger {
    position: relative;
    padding: 0;
    height: 8px;
    width: 24px;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;
    border: none;
    background: transparent;
    cursor: pointer;
}

.burger_one {
    position: absolute;
    transform: translate(50%);
    width: 24px;
    height: 2px;
    background-color: var(--c-white);
    border-radius: 2px;
    transition: 0.5s;
}

.burger_two {
    width: 14px;
    height: 2px;
    background-color: var(--c-white);
    border-radius: 2px;
    transition: 0.5s;
}

.modal {
    position: absolute;
    left: 0;
    right: 0;
    top: 40px;
    justify-content: center;
    align-items: center;
    background-color: var(--c-black);
    height: calc(100% - 40px);
    z-index: 10;
}

.modal_links {
    margin-bottom: 40px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 168px;
}

.modal_link {
    font-family: "Manrope-Medium";
    font-size: 30px;
    line-height: 40px;

    border-bottom: 2px solid var(--c-black);
    transition: 0.5s;
}

.modal_link:hover {
    border-bottom: 2px solid var(--c-white);
    transition: 0.5s;
}

@media screen and (max-width: 1200px) {
    .header {
        margin: 24px 0 176px 0;
    }

    .link {
        display: none;
    }

    .burger {
        display: flex;
    }

    .burger_one, .burger_two {
        display: block;
    }
}