@import "../../assets/styles/colors.css";

.arrow {
    display: inline-block;
    cursor: pointer;
    width: 36px;
    height: 48px;
    margin-bottom: 8px;
    position: relative;
    transition: 0.5s;
}

.arrow:hover {
    height: 56px;
    margin-bottom: 0;
    transition: 0.5s;
}

.arrow:hover .stick {
    background-color: var(--c-gray);
    transition: 0.5s;
}

.arrow:hover .angle::before {
    background-color: var(--c-gray);
    transition: 0.5s;
}

.arrow:hover .angle::after {
    background-color: var(--c-gray);
    transition: 0.5s;
}

.stick {
    position: absolute;
    bottom: 0px;
    left: 18px;
    height: 48px;
    width: 2px;
    background-color: var(--c-white);
    border-radius: 1px;
    transform: translate(-50%, 0);
    transition: 0.5s;
}

.angle::before {
    content: '';
    position: absolute;
    bottom: 8px;
    left: -2px;
    width: 24px;
    height: 2px;
    border-radius: 1px;
    background-color: var(--c-white);
    transform: rotate(45deg);
    transition: 0.5s;
}

.angle::after {
    content: '';
    position: absolute;
    bottom: 8px;
    left: 14px;
    width: 24px;
    height: 2px;
    border-radius: 1px;
    background-color: var(--c-white);
    transform: rotate(-45deg);
    transition: 0.5s;
}

@media screen and (max-width: 1200px) {
    .arrow {
        width: 24px;
        height: 32px;
        margin-bottom: 4px;
    }

    .arrow:hover {
        height: 36px;
        margin-bottom: 0;
    }

    .stick {
        height: 32px;
        left: 12px;
    }

    .angle::before {
        width: 16px;
        bottom: 5px;
        left: -1px;
    }

    .angle::after {
        width: 16px;
        bottom: 5px;
        left: 9px;
    }
}