@import "../../assets/styles/fonts.css";
@import "../../assets/styles/colors.css";

.footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 60px;
}

.footer__copyright {
    font-family: "Manrope-Light";
    font-size: 20px;
    line-height: 20px;

    color: var(--c-white);
}

@media screen and (max-width: 1161px) {
    .footer {
        margin-bottom: 27px;
    }

    .footer__copyright {
        font-size: 14px;
        line-height: 20px;
    }
}