@import "../../assets/styles/fonts.css";
@import "../../assets/styles/colors.css";

.mainTitle {
    width: 915px;
    height: 435px;
    margin-bottom: 185px;
    background: url("../../assets/img/Start Fellows.svg") no-repeat;
    background-size: 90%;
}

.aboutPreface {
    font-family: "Manrope-Medium";
    text-transform: uppercase;
    font-size: 20px;
    line-height: 28px;

    color: var(--c-gray);
    margin: 180px 0 16px 0;
}

.aboutText {
    font-family: "Manrope-Light";
    font-size: 40px;
    line-height: 60px;

    color: var(--c-white);
    margin: 0 0 80px 0;
}

.about, .about_today {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto 126px auto;
    max-width: 1040px;
}

.about_today {
    margin-bottom: 180px;
}

.about__img, .about__img_today {
    background: url("../../assets/img/Zinger.png") no-repeat;
    background-size: 100%;
    height: 457px;
    width: 457px;
}

.about__img_today {
    background-image: url("../../assets/img/Office.png");
}

.about__text, .product__text, .contacts__text, .contacts__text_margin {
    font-family: "Manrope-Light";
    font-size: 24px;
    line-height: 36px;

    color: var(--c-white);
    max-width: 457px;
}

.about__title {
    font-family: "Manrope-Medium";
    font-size: 48px;
    line-height: 56px;

    color: var(--c-white);
    margin-bottom: 32px;
}

.about__first {
    margin-bottom: 16px;
}

.product {
    margin-bottom: 180px;
}

.product__title, .contacts__title {
    font-family: "Manrope-Medium";
    font-size: 80px;
    line-height: 110px;

    color: var(--c-white);
    margin-bottom: 16px;
}

.product__text {
    max-width: 854px;
    margin-bottom: 80px;
}

.cards {
    display: flex;
    justify-content: space-between;
    color: var(--c-white)
}

.SomeSay, .FaceCat {
    width: 560px;
    height: 840px;
    background: rgba(255, 255, 255, 0.08);
    border-radius: 40px;
    padding: 113px 100px 91px 100px;
    cursor: pointer;
    transition: 0.5s;
}

.SomeSay:hover, .FaceCat:hover {
    background: rgba(255, 255, 255, 0.16);
    transition: 0.5s;
}

.FaceCat {
    padding: 75px 100px 91px 100px;
}

.SomeSay__logo {
    width: 300px;
    height: 268px;
    background: url("../../assets/img/SomeSay.png") no-repeat;
    background-size: 100%;
    margin: 0 auto 99px auto;
}

.FaceCat__logo {
    width: 237px;
    height: 327px;
    background: url("../../assets/img/FaceCat.png") no-repeat;
    background-size: 100%;
    margin: 0 auto 78px auto;
}

.cards__title {
    font-family: "Manrope-Medium";
    font-size: 48px;
    line-height: 56px;

    margin-bottom: 24px;
}

.cards__text, .cards__text_someSay {
    font-family: "Manrope-Light";
    font-size: 20px;
    line-height: 36px;

    margin-bottom: 85px;
}

.cards__arrow {
    position: relative;
    width: 48px;
    height: 33px;
    transition: 0.5s;
}

.cards__stick {
    position: absolute;
    background: var(--c-white);
    width: 48px;
    height: 2px;
    border-radius: 1px;
    top: 16.5px;
    right: 0px;
    transform: translate(0, -50%);
}

.cards__angle::before {
    content: "";
    position: absolute;
    top: 7px;
    right: -3px;
    background: var(--c-white);
    width: 24px;
    height: 2px;
    border-radius: 1px;
    transform: rotate(45deg);
}

.cards__angle {}

.cards__angle::after {
    content: "";
    position: absolute;
    top: 24px;
    right: -3px;
    background: var(--c-white);
    width: 24px;
    height: 2px;
    border-radius: 1px;
    transform: rotate(-45deg);
}

.contacts {
    margin-bottom: 104px;
}

.contacts__title {}

.contacts__info {
    display: flex;
    justify-content: space-between;
}

.contacts__text, .contacts__text_margin {
    max-width: 536px;
}

@media screen and (max-width: 1200px) {
    .mainTitle {
        margin: 0 0 160px 16px;
        width: 257px;
        height: 136px;
        background: url("../../assets/img/Start Fellows.svg") no-repeat;
    }

    .aboutPreface {
        font-size: 14px;
        line-height: 28px;

        margin: 75px 0 16px 0;
    }

    .aboutText {
        font-size: 24px;
        line-height: 32px;

        max-width: 512px;
        margin-bottom: 56px;
    }

    .about {
        flex-direction: column;
        margin-bottom: 56px;
    }

    .about_today {
        flex-direction: column-reverse;
        margin-bottom: 108px;
    }

    .about__img {
        width: 288px;
        height: 180px;
        background: url("../../assets/img/Zinger-mob.png") no-repeat;
        background-size: 100%;
        margin-bottom: 32px;
    }

    .about__img_today {
        width: 288px;
        height: 180px;
        background-size: 100%;
        background-image: url("../../assets/img/Office-mob.png");
        margin-bottom: 24px;
    }

    .about__text, .product__text, .contacts__text, .contacts__text_margin {
        font-size: 16px;
        line-height: 28px;

        max-width: 288px;
    }

    .about__title {
        font-size: 30px;
        line-height: 40px;

        margin-bottom: 12px;
    }

    .about__first {
        margin-bottom: 8px;
    }

    .product {
        margin-bottom: 108px;
    }

    .product__title, .contacts__title {
        font-size: 44px;
        line-height: 48px;
        margin-bottom: 16px;
    }

    .product__text {
        max-width: 512px;
    }

    .cards {
        flex-direction: column;
        align-items: center;
    }

    .SomeSay, .FaceCat {
        height: 463px;
        width: 287px;
        padding: 65px 41px 36px 41px;
        border-radius: 20px;
    }

    .FaceCat {
        padding: 40px 41px 36px 41px;
    }

    .SomeSay {
        margin-bottom: 32px;
    }

    .SomeSay__logo {
        width: 154px;
        height: 137px;
        margin-bottom: 50px;
    }

    .FaceCat__logo {
        width: 136px;
        height: 188px;
        margin-bottom: 25px;
    }

    .cards__title {
        font-size: 30px;
        line-height: 40px;

        margin-bottom: 16px;
    }

    .cards__text, .cards__text_someSay {
        font-size: 14px;
        line-height: 22px;

        margin-bottom: 47px;
    }

    .cards__text_someSay {
        margin-bottom: 27px;
    }

    .cards__arrow {
        height: 16px;
        width: 24px;
    }

    .cards__stick {
        width: 24px;
    }

    .cards__angle:before {
        content: "";
        width: 12px;
        right: -2px;
        top: 11.5px;
    }

    .cards__angle {}

    .cards__angle:after {
        content: "";
        width: 12px;
        right: -2px;
        top: 19px
    }

    .contacts {
        margin-bottom: 72px;
    }
    .contacts__title {}
    .contacts__info {
        flex-direction: column;
        align-items: center;
    }

    .contacts__text_margin {
        margin-bottom: 24px;
    }
}

@media screen and (max-width: 550px) {
    .aboutText {
        max-width: 272px;
    }

    .product__text, .contacts__text, .contacts__text_margin {
        max-width: 288px;
    }
}